button {
    padding: 5px 10px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: .151s;
    transition-delay: .151s;
    margin: 0 8px;
    box-shadow: 1px 1px 5px black;
    text-transform: uppercase;
}

button:hover {
    text-shadow: 1px 1px 1px black;
}

